export const Chart = ({ children, children1, width, height }) => (
  <svg viewBox={`0 0 ${width} ${height}`} width={width} height={height}>
    {children}
    {children1}
  </svg>
);

export const Bar = ({ fill = '#000', x, y, width, height }) => (
  <rect fill={fill} x={x} y={y} width={width} height={height} />
);
